/* =========================================
              Website Resets
============================================ */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
  font-family: 'Montserrat', sans-serif !important;

}

*::-moz-focus-inner {
  border: 0;
}

*:focus {
  outline: none;
}

html {
  font-size: 83.375%;
}

